:root {
    /* COLORS */
    --white: #f3f3f3;
    --black: #222222;
    --light-grey: #E2E2E2;
    --grey: #999999; 

    /* DIMENSIONS */
    --mobile: 0;
    --mobile-landscape: 700px;
    --tablet: 1024px;
    --desktop: 1200px;
    
    /* MEDIA QUERIES */
    @custom-media --mobile-viewport (min-width: 0px);
    @custom-media --mobile-landscape-viewport (min-width: 700px);
    @custom-media --tablet-viewport (min-width: 1024px);
    @custom-media --desktop-viewport (min-width: 1200px);
}

/* RESETS */
html, body {
    margin: 0;
    padding: 0;
}
body {
    font-size: 10px;
    max-width: var(--tablet);
    margin: 0 auto;
}@media (--mobile-landscape-viewport) {
    .row {
        width: 100%;
        display: table;
        table-layout: fixed;
    }
    .col {
        display: table-cell;
        vertical-align: top;
    }
    .col-33 {
        width: 33%;
    }
}h1 {
    margin: 0rem;
    font-size: 2rem;
}.body {
     font-size:1rem;
     margin:0;
     padding:0;
 }

 .content {
     margin:0;
     padding:2rem;
 }.menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
.menu ul li a {
    line-height: 2rem;
    display: block;
    text-decoration: none;
    text-indent: 1rem;
    color: var(--black);
    background-color: var(--light-grey);
}
.menu ul li a:hover {
    color: var(--white);
    background-color: var(--black);
}.logo {
    padding: 10px;
}